.main-red{
  @include respondTo(desktop) {
    overflow-x:visible;
  }
  @include respondTo(mobile) {
    overflow-x:hidden;
  }
}
.block-container{
  padding-left: .5rem;
  padding-right: .5rem;
  @include respondTo(desktop) {
    padding-left: 4rem;
    padding-right: 4rem;
  }
  @include respondTo(mobile) {
    padding-left:4.062rem;
    padding-right:4.062rem;
  }
  @include respondTo(small) {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
  }
}
.block-menu{
  position: relative;
  padding-top: 2.688rem;
  padding-bottom:3.688rem;
  @include respondTo(mobile) {
    padding-top: 3rem;
    padding-bottom: 1.625rem;
  }
  @include respondTo(small) {
    padding-top: 2rem;
    padding-bottom: 2.375rem;
  }
}
.block-elips{
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height:32.375rem;
  @include respondTo(mobile) {
    display: none;
  }
}
.color-ellipse {
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &:first-child{
    width: 63%;
    height: 100%;
    top: 0;
    left: 0;


  }
  &:nth-child(2){
    width: 50%;
    top: 0;
    right: 0;
    height: 100%;
    opacity: 0.5;
    background-position: 50% 5%;
    //background: linear-gradient(180deg,#30ff0e,#baff61);
    //filter: blur(800px);
    //opacity: 0.2;
  }

}
.card-block-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -.625rem;
  margin-right: -.625rem;
  margin-bottom: -1.25rem;

  @include respondTo(mobile) {
    margin-left: -.5rem;
    margin-right: -.5rem;
    margin-bottom: -1rem;
  }
  @include respondTo(small) {
    margin-left: -.5rem;
    margin-right: -.5rem;
    margin-bottom: -1rem;
  }

  &__col {
    margin: 0 .625rem 1.25rem;//0 10px 1.25rem

    // @include respondTo(tablet) {
    //     margin: 0 .625rem 1.25rem;//0 10px 1.25rem
    // }

    @include respondTo(mobile) {
      margin: 0 .5rem 1rem;//0 8px 1rem
    }
  }

  .card-list__col.full-banner {
    width: 100%;
  }

  &_only-one-row {

    &.card-list_4-col {

      .card-list__col:nth-of-type(n + 5) {
        display: none;
      }

      @include respondTo(desktop) {

        .card-list__col:nth-of-type(n + 4) {
          display: none;
        }
      }

      @include respondTo(tablet) {

        .card-list__col:nth-of-type(n + 3) {
          display: none;
        }
      }
    }

    &.card-list_3-col {

      .card-list__col:nth-of-type(n + 4) {
        display: none;
      }

      @include respondTo(desktop) {

        .card-list__col:nth-of-type(n + 3) {
          display: none;
        }
      }
    }
  }

  &_4-col {

    .card-list__col {
      width: calc(25% - 1.25rem);

      @include respondTo(desktop) {
        width: calc(33.3333% - 1.25rem);
      }

      @include respondTo(tablet) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(mobile) {
        width: calc(100% - 1rem);
      }
    }
  }

  &_3-col {

    .card-list__col {
      width: calc(33.3333% - 1.25rem);

      @include respondTo(desktop) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(tablet) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(mobile) {
        width: calc(100% - 1rem);
      }
    }
  }

  &_2-col {

    .card-list__col {
      width: calc(50% - 1.25rem);

      @include respondTo(desktop) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(tablet) {
        width: calc(50% - 1.25rem);
      }

      @include respondTo(mobile) {
        width: calc(100% - 1rem);
      }
    }
  }
}
.h1-title{
  position: relative;
  z-index: 5;
  font-size: 2.875rem;
  line-height: 4rem;
  color: $color-bg-black;
  font-family: $ff-bold;
  text-align: center;

  @include respondTo(mobile) {
    font-size: 3.5rem;
    line-height: 3.5rem;
    width: 100%;
    max-width:40.375rem;
    margin: 0 auto;
  }
  @include respondTo(small) {
    font-size: 2rem;
    line-height: 2.25rem;
  }

}
.field-search{
  position: relative;
  &__mane-page{
    position: relative;
    z-index: 5;
    margin-top: 2rem;
    @include respondTo(mobile) {
      margin-top: 3rem;
    }
    @include respondTo(small) {
      margin-top: 2.438rem;
    }
    .search__input{
      box-shadow: 0 1.25rem 3.75rem rgba(107, 112, 129, 0.15);
      @include respondTo(mobile) {
        height:4rem;
        box-shadow: 0px 10px 30px rgba(202, 204, 223, 0.5);
      }
      @include respondTo(small) {
        height:2.5rem;
      }

    }
  }
  &__form {
    border-radius:2.313rem;
    margin-bottom: 0;
  }
  .search__input{
    border-radius:2.313rem;
  }
}
.round-cards{
  &__answer-questions{
    position: relative;
    z-index: 1;
    margin-top:1.688rem;
    justify-content: flex-end;
    .round-cards__item{
      margin: 0 0.313rem .5rem;
      font-size: .75rem;
    }
    @include respondTo(mobile) {
      justify-content: flex-start;
      margin-top: 1.188rem;
    }
    @include respondTo(small) {
      margin-top:0.75rem;
    }

  }
}
.block-services{
  &__main{
    position: relative;
    z-index: 5;
    margin-top:3.063rem;
    @include respondTo(small) {
      margin-top:2.375rem;
    }
  }
  width: 100%;
  max-width: 66.5rem;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: minmax(12.5rem,38.938rem) minmax(12.5rem,29.063rem);
  grid-column-gap: 1.875rem;
  @include respondTo(small) {
    grid-template-columns:1fr;
  }
  &__col{
    &:nth-child(odd){
      padding-left: 1.25rem;
      @include respondTo(tablet) {
        padding-left: 0;
      }
    }
    &:nth-child(even){
      padding-left:3rem;
      @include respondTo(tablet) {
        padding-left: 0;
      }
    }

  }
  &__item{
    width: 100%;
    max-width:29.063rem;
    display: flex;
    align-items: center;
    margin-bottom: 2.4rem;
    &:hover{
      .block-services__item-title{
        color: #67a511;
      }
    }
    @include respondTo(small) {
      margin-bottom:1.25rem;
    }
  }
  &__item-img{
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 5.3rem;
    margin-right:1.813rem;
    @include respondTo(mobile) {
      max-width:4.375rem;
      margin-right: 1.5rem;
    }
    @include respondTo(small) {
      max-width: 2.562rem;
      margin-right: 1.938rem;
    }
  }
  &__item-wrap-des{

  }
  &__item-title{
    font-size: 1.5rem;
    line-height: 1.6rem;
    letter-spacing: -0.015em;
    color: #1C1B28;
    font-family: $ff-bold;
    transition-duration: .2s;
    @include respondTo(tablet) {
      font-size: 1.25rem;
      line-height:1.313rem;
    }
    @include respondTo(small) {
      font-size:1.125rem;
      line-height:1.188rem;
    }
  }
  &__item-des{
    margin-top: 0.75rem;
    font-size: 1rem;
    line-height: 1.2rem;
    color: #686D82;
    @include respondTo(tablet) {
      font-size: 1rem;
      line-height:1.438rem;
      margin-top:0.625rem;
    }
    @include respondTo(mobile) {
      font-size:0.813rem;
      line-height:1.188rem;
    }

    @include respondTo(small) {
      font-size:0.75rem;
      line-height: 1rem;
    }
  }
}
//-------------------------------------
.block-possibilities{
  padding-top:4.438rem;
  padding-bottom:6.75rem;
  @include respondTo(mobile) {
    padding-top:3.125rem;
    padding-bottom:2.75rem;
  }
  @include respondTo(small) {
    padding-top:1.75rem;
    padding-bottom:1.75rem;
  }
}

//-----------------------------------
.block-popular-instructions{
  margin-top:2.813rem;
  .headline-row{
    margin-bottom:2.313rem;
  }
  @include respondTo(mobile) {
    display: none;
  }
}
.block-appKi{
  padding-top:4.5rem;
  padding-bottom:7rem;
  .headline-row{
    margin-bottom:4.25rem;
    min-height: 3.75rem;
    @include respondTo(mobile) {
      margin-bottom:1.562rem;
    }
    @include respondTo(small) {
      margin-bottom: 1.25rem;
    }
  }

  @include respondTo(mobile) {
    padding-top:3rem;
    padding-bottom:3rem;
  }
  @include respondTo(small) {
    padding-top:2rem;
    padding-bottom:2rem;
  }
  .key-indicators-select__choosen{
    @include respondTo(mobile) {
      border-radius:0.75rem;
    }
    .key-indicators{
      @include respondTo(mobile) {
        border-radius:0.75rem;
      }
    }

  }
}
//----------------------
.block-all-question{
  padding-top:4.062rem;
  padding-bottom:3.938rem;
  .headline-row{
    margin-bottom:2.063rem;
    @include respondTo(small) {
      margin-bottom:1.5rem;
    }
  }
  @include respondTo(mobile) {
    padding-top:3rem;
    padding-bottom:3rem;
  }
  @include respondTo(small) {
    padding-top:2rem;
    padding-bottom:2rem;
  }
}
.block-news{
  padding-top:4.062rem;
  padding-bottom:3.938rem;
  .card-news__tags{
    min-height:2.375rem;
  }
  .card-news__img {
    margin-bottom:0.625rem;
  }
  .card-news__tags{
    margin-bottom: 0;
  }
  @include respondTo(mobile) {
    padding-top:3rem;
    padding-bottom:3rem;
  }
  @include respondTo(small) {
    padding-top:2rem;
    padding-bottom:2rem;
  }
}
.block_feedback-main{
  padding-top:0.75rem;
  padding-bottom:4.25rem;
  .feedback {
    max-width: 37.25rem;
  }
  .headline-row{
    margin-bottom:1.438rem;
  }
}
//-------------------------------
.media-menu-btn {
  opacity: 0;
  position: absolute;
  right: 0;
  //right: calc(50% + 39.375rem);
  transition: opacity .3s,visibility .3s;
  visibility: hidden;
  top:1.688rem;
  z-index: 11;
  @include respondTo(desktop) {
    visibility: visible;
    opacity: 1;
  }
  @include respondTo(mobile) {
    top: 20px;
    right: 20px;
  }
  @include respondTo(small) {
    top: 12px;
    right: 20px;
  }
}
.header-buttons-main{
  .header-buttons__connection{
    @include respondTo(desktop) {
      right:4.125rem;
      bottom:1.313rem;
    }
    @include respondTo(mobile) {
      right: 60px;
      bottom: 26px;
    }
    @include respondTo(small) {
      right: 60px;
      bottom:14px;
    }



  }
}
.block-banners{
  .card-service__circle{
    transition-duration: .5s;
  }
  .card-list__col{
    &:hover .card-service__circle{
      transform: scale(1.1);
    }
  }
}
